@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=League+Script&family=Yeseva+One&family=Poppins:wght@300;400;800&display=swap");
.animation {
  /* 텍스트 */
  /* SVG */
}
.animation_section {
  margin-top: 100px;
  margin-bottom: 100px;
}
.animation_section:first-child {
  margin-top: 0;
}
.animation_content_wrap {
  position: relative;
  font-weight: bold;
  font-size: 60px;
  word-break: break-all;
  font-family: "Poppins", sans-serif;
}
.animation_title {
  margin-bottom: 10px;
}
.animation_typing_text {
  border-right: 0.08em solid #000;
}
.animation_gradient_text {
  color: #f35626;
  background-image: linear-gradient(92deg, #f35626, #feab3a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: hue 10s infinite linear;
}
@keyframes hue {
  from {
    -webkit-filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(-360deg);
  }
}
.animation_gradient_move_text {
  font-size: 60px;
  background-image: linear-gradient(90deg, rgb(94, 114, 235) 0%, rgb(255, 145, 144) 56%, rgb(254, 193, 149) 100%);
  color: transparent;
  -webkit-background-clip: text;
  animation: gradientmove 1s infinite;
}
@keyframes gradientmove {
  50% {
    background-image: linear-gradient(262deg, rgb(94, 114, 235) 0%, rgb(255, 145, 144) 56%, rgb(254, 193, 149) 100%);
  }
}
.animation_linear_text {
  position: relative;
  color: rgba(0, 0, 0, 0.3);
  word-break: keep-all;
  font-size: 32px;
  font-weight: bold;
}
@media screen and (min-width: 768px) {
  .animation_linear_text {
    font-size: 60px;
  }
}
.animation_linear_text::before {
  content: attr(data-text);
  position: absolute;
  overflow: hidden;
  max-width: 7em;
  white-space: nowrap;
  word-break: break-all;
  color: #6eb327;
  animation: lineartext 8s linear infinite;
}
@keyframes lineartext {
  0% {
    max-width: 0;
  }
}
.animation_shining_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  overflow: hidden;
  background-image: linear-gradient(90deg, #000, #fff, #000);
  background-repeat: no-repeat;
  background-size: 70%;
  animation: shining 3s linear infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
}
.animation_shining_text_wrap {
  position: relative;
  background-color: #000;
  padding: 0 20px;
  min-height: 350px;
  width: 100%;
  box-sizing: border-box;
}
@keyframes shining {
  0% {
    background-position: -500%;
  }
  100% {
    background-position: 500%;
  }
}
.animation_glitch_text {
  width: 100%;
  min-height: 350px;
  background-color: black;
}
.animation_glitch_text_wrapp {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.animation_glitch_text .glitch {
  text-decoration: none;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  color: #fff;
  letter-spacing: 5px;
}
.animation_glitch_text .glitch::before, .animation_glitch_text .glitch::after {
  display: block;
  content: attr(data-glitch);
  text-transform: uppercase;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.8;
}
.animation_glitch_text .glitch::after {
  color: #f0f;
  z-index: -2;
  animation: glitch 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both infinite;
}
.animation_glitch_text .glitch::before {
  color: #0ff;
  z-index: -1;
  animation: glitch 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) reverse both infinite;
}
@keyframes glitch {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-5px, 5px);
  }
  40% {
    transform: translate(-5px, -5px);
  }
  60% {
    transform: translate(5px, 5px);
  }
  80% {
    transform: translate(5px, -5px);
  }
  to {
    transform: translate(0);
  }
}
.animation_shadow_text_wrap {
  background: #e6eaad;
  display: flex;
  flex-direction: column;
  min-height: 350px;
  justify-content: center;
  align-items: center;
}
.animation_shadow_text .words {
  color: #e6eaad;
  font-size: 0;
  line-height: 1.5;
}
.animation_shadow_text .words span {
  font-size: 60px;
  display: inline-block;
  animation: move 3s ease-in-out infinite;
}
.animation_shadow_text .words span:nth-child(2) {
  animation-delay: 0.5s;
}
.animation_shadow_text .words span:nth-child(3) {
  animation-delay: 1s;
}
.animation_shadow_text .words span:nth-child(4) {
  animation-delay: 1.5s;
}
.animation_shadow_text .words span:nth-child(5) {
  animation-delay: 2s;
}
.animation_shadow_text .words span:nth-child(6) {
  animation-delay: 2.5s;
}
.animation_shadow_text .words span:nth-child(7) {
  animation-delay: 3s;
}
@keyframes move {
  0% {
    transform: translate(-30%, 0);
  }
  50% {
    text-shadow: 0 25px 50px rgba(0, 0, 0, 0.75);
  }
  100% {
    transform: translate(30%, 0);
  }
}
.animation_sign_text_wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background: #000;
  font-family: "Yeseva One", cursive;
  font-size: 50px;
  min-height: 350px;
}
.animation_sign_text .title {
  font-size: 110px;
}
.animation_sign_text .blink {
  animation: blink 0.01s infinite alternate;
}
.animation_sign_text .fade {
  opacity: 0.8;
  color: #ebffff;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.3), 0 0px 15px #fff, 0 0 10px #e4e9ac, 0 0 50px #e4e9ac;
  animation: fade 3s infinite alternate;
}
.animation_sign_text .neon_main {
  margin: 0 auto;
  text-align: center;
  color: #ebffff;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.3), 0 0px 15px #fff, 0 0 10px #e4e9ac, 0 0 50px #e4e9ac;
}
.animation_sign_text .neon_sub {
  font-family: "League Script", sans-serif;
  font-size: 100px;
  margin: 0 auto;
  text-align: center;
  color: #ccf;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.5), 0 0 20px #fff, 0 0 10px #003770, 0 0 50px #003770;
}
@keyframes blink {
  70% {
    opacity: 0.7;
  }
}
@keyframes fade {
  40% {
    opacity: 0.8;
  }
  42% {
    opacity: 0.1;
  }
  43% {
    opacity: 0.8;
  }
  45% {
    opacity: 0.1;
  }
  46% {
    opacity: 0.8;
  }
}
.animation_light_text {
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 3rem;
  line-height: 0.75;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.animation_light_text_wrap {
  position: relative;
  background: #000;
  padding: 2rem;
  min-height: 350px;
}
.animation_light_text span {
  display: block;
}
.animation_light_text span:not(.light) {
  opacity: 0;
  animation: flashText 0.5s ease-out alternate infinite;
}
.animation_light_text span.light {
  position: relative;
  display: inline-block;
}
.animation_light_text span.light::before {
  position: absolute;
  left: 0;
  top: -10%;
  width: 100%;
  height: 120%;
  background: rgba(255, 255, 255, 0.4);
  filter: blur(10px);
  opacity: 0;
  animation: flash 0.5s ease-out alternate infinite;
  content: "";
}
@keyframes flash {
  to {
    opacity: 1;
  }
}
@keyframes flashText {
  to {
    opacity: 0.15;
  }
}
.animation_floating_text {
  width: 100%;
  min-height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: floating 5s cubic-bezier(0.37, 0, 0.63, 1) infinite;
}
.animation_floating_text_wrap {
  background: #e6eaad;
  color: #6eb327;
  overflow: hidden;
}
.animation_floating_text .title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  animation: tilting 10s cubic-bezier(0.37, 0, 0.63, 1) infinite;
}
.animation_floating_text span {
  transition: font-weight 0.4s ease-in;
  animation: floating-secondary 2.5s cubic-bezier(0.37, 0, 0.63, 1) infinite;
}
.animation_floating_text span:nth-child(1) {
  animation-delay: 1s;
}
.animation_floating_text span:nth-child(2) {
  animation-delay: 2s;
}
.animation_floating_text span:nth-child(3) {
  animation-delay: 3s;
}
.animation_floating_text span:nth-child(4) {
  animation-delay: 4s;
}
.animation_floating_text span:nth-child(5) {
  animation-delay: 5s;
}
.animation_floating_text span:nth-child(6) {
  animation-delay: 6s;
}
.animation_floating_text span:nth-child(7) {
  animation-delay: 7s;
}
.animation_floating_text span:nth-child(8) {
  animation-delay: 8s;
}
.animation_floating_text span:nth-child(9) {
  animation-delay: 9s;
}
@keyframes floating {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 5vh);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes floating-secondary {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 1vh);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes tilting {
  0% {
    transform: rotate(1deg);
  }
  50% {
    transform: rotate(-1deg);
  }
  100% {
    transform: rotate(1deg);
  }
}
.animation_slide_text {
  text-transform: uppercase;
  line-height: 1;
}
.animation_slide_text_wrap {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #6eb327;
  width: 100%;
  min-height: 350px;
}
.animation_slide_text span {
  text-align: center;
  display: block;
  width: 100%;
  color: #fff;
  clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  transform: translateY(-50px);
  opacity: 0;
  animation-name: slidetext;
  animation-timing-function: ease;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-delay: 0.6s;
  -webkit-animation-fill-mode: forwards;
}
@keyframes slidetext {
  0% {
    transform: translateY(-50px);
    opacity: 0;
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  }
  20% {
    transform: translateY(0);
    opacity: 1;
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  80% {
    transform: translateY(0);
    opacity: 1;
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  100% {
    transform: translateY(50px);
    opacity: 0;
    clip-path: polygon(100% 0, 100% 0%, 0 100%, 0 100%);
  }
}
.animation_slide_text span:first-child {
  animation-delay: 0.7s;
}
.animation_slide_text span:last-child {
  animation-delay: 0.5s;
}
.animation_svg_line {
  width: 200px;
  fill: #6eb327;
}
.animation_svg_line_path {
  fill-opacity: 0;
  stroke: #000;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-dasharray: 860;
  stroke-dashoffset: 860;
  animation: draw 5s linear forwards infinite;
}
@keyframes draw {
  to {
    fill-opacity: 1;
    stroke: #6eb327;
    stroke-dashoffset: 0;
  }
}
.animation_svg_dash_line {
  width: 100%;
  height: 100%;
}
.animation_svg_dash_line_wrap {
  width: 404px;
  height: 85px;
}
.animation_svg_dash_line text {
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  animation: stroke 5s infinite alternate;
  stroke-width: 1;
  stroke: #6eb327;
  font-size: 60px;
  font-weight: bold;
}
@keyframes stroke {
  0% {
    fill: rgba(72, 138, 20, 0);
    stroke: rgb(97, 166, 14);
    stroke-dashoffset: 25%;
    stroke-dasharray: 0 50%;
    stroke-width: 1;
  }
  70% {
    fill: rgba(72, 138, 20, 0);
    stroke: rgb(97, 166, 14);
  }
  80% {
    fill: rgba(72, 138, 20, 0);
    stroke: rgbargba(97, 166, 14, 1);
    stroke-width: 2;
  }
  100% {
    fill: rgb(97, 166, 14);
    stroke: rgba(97, 166, 14, 0);
    stroke-dashoffset: -25%;
    stroke-dasharray: 50% 0;
    stroke-width: 0;
  }
}